function ProcessViewCount(viewCount) {
    if (viewCount < 1000) {
      return ` ${viewCount}`; //  100
    } else if (viewCount < 10000) {
      return ` ${(viewCount / 1000).toFixed(1)}`; //  1.1
    } else if (viewCount < 100000) {
      return ` ${(viewCount / 10000).toFixed(1)}`; //  1.3
    } else if (viewCount < 100000000) {
      return ` ${(viewCount / 10000).toFixed(0)}`; //  103
    } else {
      return ` ${(viewCount / 100000000).toFixed(0)}`; //  3
    }
}
  
export { ProcessViewCount };