import React, { useState } from 'react';
import styles from "./Header.module.css";
import youtube_logo from '../../data/youtube_logo.png';
import { AiOutlineMenu, AiOutlineAppstoreAdd, AiOutlineMore, AiOutlineSearch} from 'react-icons/ai';

const Header = ({menuToggle}) => {
    const [value, setValue] = useState('');

    function onChange(event) {
        setValue(event.target.value);
    }

    function onSearch(){
        if(!value) return
        console.log(" : "+ value);
        setValue('');
    }

    return (
        <div className={styles.header}>
            <div className={`${styles.flex}`}>
                <AiOutlineMenu className={styles.icon} onClick={menuToggle} />
                <img src={youtube_logo} alt="" className={styles.logo} />
            </div>
            <div className={styles['search-box']}>
                <input className={styles.input} value={value} onChange={onChange} />
                <AiOutlineSearch className={styles['icon-search']} onClick={onSearch} />
            </div>
            <div className={`${styles.flex}`}>
                <AiOutlineAppstoreAdd className={styles.icon} />
                <AiOutlineMore className={styles.icon} />
            </div>
        </div>
    );
};

export default Header;