import React, { useState } from 'react';
import Layout from '../components/common/Layout';
import HomeCard from '../components/home/HomeCard';
import HomeFilter from '../components/home/HomeFilter';
import youtubeData from '../data/youtubeData.json';
import styles from './Home.module.css';

const target = ['', 'BTS', 'LISA', ''];

const Home = () => {
    const [filter, setFilter] = useState('');

    function mapFunc(data, index) {
        return (
        <HomeFilter
            filter={filter}
            text={data}
            onClickFilter={function () {
            setFilter(data);
            }}
            key={`home-filter-${index}`}
        />
        );
    }

    function filterFunc(data) {
        if(
            filter === '' ||
            data.title.includes(filter) ||
            data.description.includes(filter)
        ) {
            return true;
        }
        return false;
    }

    return (
        <Layout isActive="home">
            <div className={styles.header}>{target.map(mapFunc)}</div>
            <div className={styles.container}>
                <div className={styles.grid}>{youtubeData['data'].filter(filterFunc).map(HomeCard)}</div>
            </div>
        </Layout>
    );
};

export default Home;