import React from 'react';
import ContentsLayout from '../components/common/ContentsLayout';
import Layout from '../components/common/Layout';
import sortedYoutubeData from '../data/sortedYoutubeData.json';
import SubscriptionCard from '../components/subscription/SubscriptionCard'
import moment from 'moment';

const Subscription = () => {
    console.log(' : ', moment().year());
  console.log(' : ', moment().month() + 1);
  console.log(' : ', moment().date());
  console.log(' : ', moment().hour());
  console.log(' : ', moment().minute());
  console.log(' : ', moment().second());

  //  : --T--
  const time1 = moment('2021-09-10T08:00:33');
  const time2 = moment('2021-09-24T08:00:33');
  const time3 = moment('2021-09-24T09:50:33');

  console.log('time1  : ', time1.fromNow());
  console.log('time2  : ', time2.fromNow());
  console.log('time3  : ', time3.fromNow());
    return (
        <Layout isActive="subscription">
            <ContentsLayout>
                {sortedYoutubeData['data'].map(function (data, index) {
                return (
                    <SubscriptionCard data={data} key={`subscription-card-${index}`} />
                );
                })}
            </ContentsLayout>
        </Layout>
    );
};

export default Subscription;