import React from 'react';
import styles from "./Menu.module.css";
import { AiOutlineHome, AiOutlineMonitor, AiOutlineFolderOpen, AiOutlineFileText } from "react-icons/ai";
import { Link } from 'react-router-dom';

const Menu = ({isActive}) => {
    //console.log(isActive)
    return (
        <ul className={styles.menu}>
            <li>
                <Link to="/" className={`${styles.link} ${isActive === 'home' ? styles.active : ""}`}>
                    <AiOutlineHome className={styles.icon} />
                    <span className={styles.text}></span>
                </Link>
            </li>
            <li>
                <Link to="/explore" className={`${styles.link} ${isActive === 'explore' ? styles.active : ""}`}>
                    <AiOutlineMonitor className={styles.icon} />
                    <span className={styles.text}></span>
                </Link>
            </li>
            <li>
                <Link to="/subscription" className={`${styles.link} ${isActive === 'subscription' ? styles.active : ""}`}>
                    <AiOutlineFolderOpen className={styles.icon} />
                    <span className={styles.text}></span>
                </Link>
            </li>
            {/* <li>
                <Link to="/assignment" className={`${styles.link} ${isActive === 'assignment' ? styles.active : ""}`}>
                    <AiOutlineFileText className={styles.icon} />
                    <span className={styles.text}></span>
                </Link>
            </li> */}
        </ul>
    );
};

export default Menu;