import React, { useState } from 'react';
import ContentsLayout from '../components/common/ContentsLayout';
import Layout from '../components/common/Layout';
import HorizontalCard from '../components/shared/HorizontalCard';
import youtubeData from '../data/youtubeData.json';

import styles from "../components/common/Layout.module.css";

const Explore = () => {
    const [list, setList] = useState(youtubeData['data'])
    const [tab, setTab] = useState("all");

    const exploreFilter = (word) => {
        if(word != 'all'){
            let nextList = youtubeData['data'].filter(item => item.title.includes(word) || item.description.includes(word) );
            setList(nextList);
        } else{
            setList(youtubeData['data'])
        }
    }
    return (
        <Layout isActive="explore">
            <div className={styles.tags}>
                <ul className={styles['tag-list']}>
                    <li className={`${tab === 'all' ? styles.active: ''}`} onClick={() => exploreFilter('all')}></li>
                    <li className={`${tab === 'Yerin' ? styles.active: ''}`} onClick={() => exploreFilter('Yerin')}>Yerin</li>
                    <li className={`${tab === 'LeeHi' ? styles.active: ''}`} onClick={() => exploreFilter('LeeHi')}>LeeHi</li>
                    <li className={`${tab === 'MAMAMOO' ? styles.active: ''}`} onClick={() => exploreFilter('MAMAMOO')}>MAMAMOO</li>
                </ul>
            </div>
            <ContentsLayout>
                {
                    list.map((data, index) => {
                        return <HorizontalCard key={`explore-card-${index}`} data={data} />
                    })
                }
            </ContentsLayout>
        </Layout>
    );
};

export default Explore;